* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

body {
  font-family: "Poppins", sans-serif;
}

#dark {
  background-color: $color-bg;
  background-image: url("../images/bg-texture.png");
  background-attachment: fixed;
  color: $color-white;
}

#light {
  background-color: $color-bg-lightmode;
  color: $color-bg;
}

section {
  min-height: 100vh;
}

button {
  font-family: "Poppins", sans-serif;
  font-size: 12px;
}

#light h1 {
  color: $color-bg;
}

h2 {
  color: $color-primary;
  font-family: "Lora", serif;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 40px;
}

#light h2 {
  color: $color-bg;
}

h3 {
  width: 100%;
  padding: 5px 10px;
  margin: 0px auto 10px;
  text-align: center;
  background: $color-bg-variant;
  border-radius: 5px;
}

#light h3 {
  color: $color-white;
  border-image: linear-gradient($color-primary, $color-bg-variant);
  border-image-slice: 1;
}
