.expcard {
  transition: 0.2s;
  height: 150px;
  width: 200px;
  background: linear-gradient($color-light, $color-bg-variant);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 2px lightgrey solid;
  color: white;

  &__img {
    background: white;
    border-radius: 50px;
    display: flex;
    align-items: center;

    & img {
      height: 80px;
      width: 80px;
    }
  }
  &:hover {
    scale: (1.02);
    box-shadow: 1px 1px 5px $color-primary;
  }
}
