.dark_mode {
  margin-top: -20px;
  // margin-left: 10px;
}

.dark_mode_label {
  width: 50px;
  height: 26px;
  position: relative;
  display: block;
  background: #242424;
  border-radius: 200px;
  box-shadow: inset 0px 5px 15px rgba(0, 0, 0, 0.4),
    inset 0px -5px 15px rgba(255, 255, 255, 0.4);
  cursor: pointer;
  transition: 0.3s;
}
.dark_mode_label:after {
  content: "";
  width: 23px;
  height: 23px;
  position: absolute;
  top: 1px;
  left: 0px;
  background: linear-gradient(180deg, #777, #3a3a3a);
  border-radius: 180px;
  transition: 0.3s;
}
.dark_mode_input {
  width: 0;
  height: 0;
  visibility: hidden;
}
.dark_mode_input:checked + .dark_mode_label {
  background: #ebebeb;
}
.dark_mode_input:checked + .dark_mode_label:after {
  left: 50px;
  transform: translateX(-100%);
  background: linear-gradient(180deg, #ffcc89, #d8860b);
}
.dark_mode_label:active:after {
  width: 30px;
}

.dark_mode_label svg {
  position: absolute;
  width: 20px;
  top: 4px;
  z-index: 100;
}
.dark_mode_label svg.sun {
  left: 29px;
  fill: #7e7e7e;
  transition: 0.3s;
}
.dark_mode_label svg.moon {
  left: 2px;
  fill: #fff;
  transition: 0.3s;
}
.dark_mode_input:checked + .dark_mode_label svg.sun {
  fill: #fff;
}
.dark_mode_input:checked + .dark_mode_label svg.moon {
  fill: #7e7e7e;
}
