.section__presentation {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0 160px 0;

  @media (max-width: 992px) {
    margin: 10px 0 30px 0;
  }

  &__text {
    padding: 25px;
    text-align: center;
    margin-bottom: 30px;
    font-size: 18px;

    & span {
      color: $color-primary;
    }
  }

  &__picture {
    background: linear-gradient($color-primary, transparent);
    width: 22rem;
    height: 420px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    border-radius: 50% 50% 0 0;

    @media (max-width: 470px) {
      width: 19rem;
      height: 360px;
    }

    & img {
      width: 100%;
      border-radius: 10px;
    }
  }
}
