.up-button {
  position: fixed;
  bottom: 25px;
  right: 12px;
  background: $color-bg;
  border: solid 1px $color-primary;
  color: $color-primary;
  font-weight: 900;
  font-size: 17px;
  width: 40px;
  height: 40px;
  display: block;
  border-radius: 6px;
  cursor: pointer;
  display: inline-block;

  & i {
    transition: 0.3s;
    transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
  }

  &:hover > i {
    transform: translateY(-3px);
  }
}
