.section__experience {
  .background-skill {
    padding: 25px;
    background-image: url("../../images/wallpaper-dev.jpg");
    background-size: cover;

    @media (max-width: 768px) {
      background-image: none;
    }
  }

  &__card {
    margin-top: 30px;
    margin-bottom: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }

    &__split {
      background: linear-gradient($color-primary-variant, $color-bg);
      gap: 30px;
      padding: 20px;
      border-radius: 10px;
      display: flex;
      flex-direction: column;

      &__display {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-wrap: wrap;
        gap: 20px;
      }
    }
  }
}
