.header {
  background: linear-gradient($color-primary, $color-bg);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 100;
  padding-left: 40px;
  padding-right: 14px;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.15);

  & .title {
    padding: 10px;

    & span {
      font-weight: bold;
      font-size: 20px;
    }

    @media (max-width: 600px) {
      font-size: 14px;
    }
  }
}

#light .header {
  background: linear-gradient($color-primary, $color-bg-lightmode);
}
