.projetcard {
  flex-basis: 25%;
  flex-grow: 1;
  min-width: 220px;
  border-radius: 5px;
  padding: 20px;
  background: linear-gradient($color-primary-variant, $color-bg);
  position: relative;
  overflow: hidden;
  transition: 1s;

  &:hover > &__img {
    scale: (1.05);
  }

  &:hover > &__content > .link-out {
    bottom: 0;
  }

  &__img {
    transition: 0.4s;

    & img {
      width: 100%;
      object-fit: cover;
      height: 100%;
    }
  }

  &__content {
    position: relative;
    color: white;
    & p {
      margin-top: 20px;
      margin-bottom: 20px;
      padding: 5px;
      border-radius: 5px;
      border: solid 1px $color-primary;
      background: linear-gradient($color-primary-variant, $color-bg-variant);
      font-size: 14px;
      text-align: center;
    }

    &__techno {
      & h4 {
        text-align: center;
      }

      &__logo {
        margin: 10px;
        display: flex;
        justify-content: space-around;

        img {
          width: 50px;
          background: white;
          border-radius: 50px;
          border: solid $color-primary 3px;
        }
      }
    }

    & .link-out {
      position: absolute;
      width: 100%;
      transform: scale(1.05);
      height: 100%;
      bottom: -140%;
      background: rgba(77, 181, 255, 0.9);
      left: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      transition: 0.7s;
      border: 2px lightgrey solid;
      border-radius: 10px;

      & .btn-portfolio {
        display: flex;
        align-items: center;
        font-size: 11px;
        margin: 3px;
        color: $color-primary;
        background: $color-bg;
        padding: 8px;
        border-radius: 5px;
        cursor: pointer;
        color: $color-primary;
        position: relative;
        z-index: 1;
        overflow: hidden;
      }

      & .btn-portfolio:hover {
        color: black;
      }

      & .btn-portfolio::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background: white;
        border-radius: 5px;
        z-index: -1;
        transition: transform 0.5s;
        transform-origin: 0 0;
        transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
        transform: scaleX(0);
      }

      & .btn-portfolio:hover::before {
        transform: scaleX(1);
      }
    }
  }
}
